@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);

/*-----------variables-----*/
/*--------------------------------colors------------------------------*/
:root{
  --primary: rgb(255, 255, 255);
  --primary-green:rgb(12,181,87);
  --bg-one:rgb(19,19,29);
  --bg-two:rgb(27,26,39);

}
*{
  text-transform: capitalize;
  scroll-behavior: smooth;
}
a{
  text-decoration: none;
}
.bg1{
  background-color: rgb(19,19,29) !important;
  background-color: var(--bg-one) !important;
}
.bg2{
  background-color: rgb(27,26,39) !important;
  background-color: var(--bg-two) !important;
}
.primary-green{
  background-color: rgb(12,181,87) !important;
  background-color: var(--primary-green) !important;
}
.greenFont{
  color: rgb(12,181,87) !important;
  color: var(--primary-green) !important;
}
.bg1Font{
  color: rgb(19,19,29);
  color: var(--bg-one);
}
.bg2Font{
  color: rgb(27,26,39);
  color: var(--bg-two);
}





