/*-----------------------------varialbles-----------------------colors------------------*/


.navbar
{
  background: var(--bg-one);
  z-index: 99;
  
}
.navbar-brand,.nav-link{
  color: var(--primary) !important;
}
.nav-link:hover{
  color: var(--primary-green) !important;
}

h3 {
  color: white;
}
.greenFont {
  color: var(--primary-green) !important;
}
.viewCont {
  height: 90vh;
}
.col-8 {
  padding-top: 20vh;
}
.fs-1 {
  font-size: 4rem !important;
}
.NameClass p {
  width: 50%;
  color: var(--primary);
  font-weight: lighter;
  margin-top: 3rem;
}
.ImgClass img {
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
  min-height: 300px;
}
.ImgClass img:hover {
  -webkit-filter: none;
          filter: none;
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
}

@media screen and (max-width: 600px) {
  .ImgClass img {
    display: none;
  }

  .NameClass p {
    display: none;
  }
}
@media screen and (max-width: 1240px) {
  .ImgClass img {
    width: 50%;
    height: 50%;
  }
}


.about li{
  list-style: none;
  color: var(--primary);
  border-left: 1px solid var(--primary-green);
  padding: 1rem ;
  margin-top: 0.5rem;
}
.card-text{
  color: var(--primary) !important;
  font-weight: lighter;
  margin-bottom: 5rem;
}
.link-btn{
  font-weight: lighter;
  font-size: small;
}
.link-btn:hover{
  
  background-color: var(--bg1) !important;
  border: solid 1px var(--primary-green);
 
}
img{
  height:250px ;
  
  
}
img:hover{
  -webkit-filter: blur(2px);
          filter: blur(2px);
}
.badge{
  text-transform: uppercase;
}

.card{
  height: 100%;
  position: relative;
}
.card a {
  position: absolute;
  bottom: 0%;
  left: 0%;
  margin: 0 0 1rem 1rem;
  
}
.Contact input, .Contact textarea{
  border: 2px solid var(--primary-green) !important ;
  margin: 1rem;
  
}
input::-webkit-input-placeholder{
  color: var(--primary) !important;
  font-weight: lighter;
}
input:-ms-input-placeholder{
  color: var(--primary) !important;
  font-weight: lighter;
}
input::placeholder{
  color: var(--primary) !important;
  font-weight: lighter;
}

textarea::-webkit-input-placeholder{
  color: var(--primary) !important;
  font-weight: lighter !important;
}

textarea:-ms-input-placeholder{
  color: var(--primary) !important;
  font-weight: lighter !important;
}

textarea::placeholder{
  color: var(--primary) !important;
  font-weight: lighter !important;
}
#contact{
  height: 100vh;
}

.SucessBox{
   width: 410px;
   padding: 0.5rem;
   margin: auto;
   border-radius: 0.2rem;
   box-shadow: 1px 2px 5px black;
    
}
.SucessBox p {
  color: var(--primary);
  text-shadow: 1px 2px 5px black;
}
#exampleInputEmail1,#NameClass{
  text-transform: none;
}

.displayNONE{
  display: none;
}


.icons-row{
  margin: auto ;
}
.iconss{
  
  margin: 0.5rem 1rem 0 0;
  
}
.iconss:hover{
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}


.phone h6, .phone p{
  text-transform: lowercase;
  color: var(--primary) ;
  font-weight: lighter;
  margin: auto 0;
}
